"use client";

import Link from "next/link";
import { useMemo } from "react";
import { getBrands } from "@/api";
import { HomeCrumb } from "@/components/bread-crumb";
import { Container } from "@/components/container";
import { InfoHeader } from "@/components/div";
import { Section } from "@/components/section";
import { InfoTitle } from "@/components/title";

// Rus alifbosi (zarur bo'lsa "Ё" ni ham qo'shing):
const RUSSIAN_LETTERS = [
  "А","Б","В","Г","Д","Е","Ж","З","И","Й","К","Л","М",
  "Н","О","П","Р","С","Т","У","Ф","Х","Ц","Ч","Ш","Щ",
  "Э","Ю","Я"
];

export default function BrandsPage() {
  // getBrands() serverdan ma’lumotlarni oladi (Server Component bo'lsa ham, misol uchun)
  const brands =  getBrands();

  // Birinchi harflar bo'yicha guruhlab olish
  // Masalan, { "A": [brand1, brand2], "B": [...], ... }
  const groupedBrands = useMemo(() => {
    return brands.reduce((acc, brand) => {
      if (!brand?.title) return acc;
      const firstLetter = brand.title.charAt(0).toUpperCase();
      // Agar ruscha bo'lmagan harflar chiqib qolsa, xohishga ko'ra "?" yoki "#" degan guruhga tashlash mumkin
      if (!acc[firstLetter]) {
        acc[firstLetter] = [];
      }
      acc[firstLetter].push(brand);
      return acc;
    }, {});
  }, [brands]);

  return (
    <Container className="pb-[58px]">
      <HomeCrumb paths={[{ name: "Каталог брендов" }]} />
      <Section className="py-6 px-0 shadow-sectionShadow rounded-none">
        <InfoHeader className="mb-5">
          <InfoTitle>Каталог брендов</InfoTitle>
        </InfoHeader>

        {/* Yuqoridagi harflar navigatsiyasi */}
        <nav className="flex flex-wrap gap-2 p-4">
          {RUSSIAN_LETTERS.map((letter) => (
            <a
              key={letter}
              href={`#${letter}`}
              className="px-3 py-1 bg-gray-100 hover:bg-gray-200 rounded"
            >
              {letter}
            </a>
          ))}
        </nav>

        {/* Harflar bo'yicha brandlarni ko'rsatish */}
        <div className="p-6 space-y-8">
          {RUSSIAN_LETTERS.map((letter) => {
            const letterBrands = groupedBrands[letter] || [];
            if (letterBrands.length === 0) {
              // Agar shu harfga tegishli brand bo'lmasa, hech narsa qaytarmaymiz
              return null;
            }
            return (
              <div key={letter} id={letter}>
                <h2 className="text-xl font-bold mb-3">{letter}</h2>
                <div className="flex flex-wrap gap-4">
                  {letterBrands.map((brand) => (
                    <div key={brand.id} className="border p-3 rounded shadow-sm">
                      {brand.path ? (
                        <Link href={brand.path}>
                          <span className="text-blue-600 hover:underline">
                            {brand.title}
                          </span>
                        </Link>
                      ) : (
                        // path bo'lmasa, shunchaki matn
                        <span>{brand.title}</span>
                      )}
                    </div>
                  ))}
                </div>
              </div>
            );
          })}
        </div>
      </Section>
    </Container>
  );
}
